@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Azeret+Mono:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-section-cnt {
  z-index: 100;
  width: 100%;
  height: 100vh;
  display: flex;
  /* background-color: rebeccapurple; */
  background-image: url(/IntelliHack-2K21/static/media/bg2.87aab9cc.jpg);
  background-color: black;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.main-section-content {
  margin-left: 3vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  /* background-color: red; */
  position: relative;
}

.clock-cnt {
  margin-top: 100px;
  /* background-color: #62ea34; */
  display: flex;
  flex-direction: column;

  -webkit-animation: fadeinClockDiv 3s;
  animation: fadeinClockDiv 3s;
}

.clock-text {
  color: white;
  font-family: "Poppins", sans-serif;
  font-size: 30px;
}

@keyframes fadeinClockDiv {
  from {
    margin-left: 200px;
    opacity: 0;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

@-webkit-keyframes fadeinClockDiv {
  from {
    opacity: 0;
    margin-left: 200px;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

.main-section-main-text {
  font-size: 90px;
  color: white;
  /* font-family: "Poppins", sans-serif; */
  -webkit-animation: fadeinMainText 2s;
  animation: fadeinMainText 2s;
}

.white-logo {
  height: 200px;
  /* position: absolute; */
  top: 20px;
  -webkit-animation: fadeinMainText 2s;
  animation: fadeinMainText 2s;
}

@keyframes fadeinMainText {
  from {
    margin-left: 200px;
    opacity: 0;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

@-webkit-keyframes fadeinMainText {
  from {
    margin-left: 200px;
    opacity: 0;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

.main-section-sub-text {
  color: white;
  font-size: 28px;
  margin-top: 20px;
  color: #62ea34;
  -webkit-animation: fadeinSubText 3s;
  animation: fadeinSubText 3s;
}

@keyframes fadeinSubText {
  from {
    margin-left: 200px;
    opacity: 0;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

@-webkit-keyframes fadeinSubText {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media (max-width: 900px) {
  .main-section-cnt {
    z-index: 100;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: rebeccapurple; */
  }
  .main-section-content {
    margin-left: 0px;
    align-items: center;
    /* background-color: red; */
    position: relative;
  }
  .main-section-sub-text {
    color: white;
    font-size: 3vw;
    margin-top: 20px;
    color: #62ea34;
  }
  .main-section-main-text {
    font-size: 10vw;
    color: white;
    /* font-family: "Poppins", sans-serif; */
  }
  .clock-cnt {
    margin-top: 60px;
    align-items: center;
  }
}

@media (max-width: 700px) {
  .main-section-sub-text {
    color: white;
    font-size: 5vw;
    margin-top: 20px;
    color: #62ea34;
  }
}

@media (max-width: 600px) {
  @keyframes fadeinClockDiv {
    from {
      margin-left: 50px;
      opacity: 0;
    }
    to {
      opacity: 1;
      margin-left: 0px;
    }
  }

  @-webkit-keyframes fadeinClockDiv {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
}

@media (max-width: 500px) {
  .clock-text {
    font-size: 6vw;
  }
  .white-logo {
    height: 35vw;
  }
}

.tick {
  font-size: 50px;
  display: flex;
  /* flex-direction: column; */
}

.clock-container {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  grid-gap: 30px;
  gap: 30px;
}

.clock-box-cnt {
  display: flex;
}

@media (max-width: 900px) {
  .clock-container {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
  }
}

@media (max-width: 450px) {
  .tick {
    font-size: 12vw;
    display: flex;
    /* flex-direction: column; */
  }
}

.navbar-cnt {
  display: flex;
  width: 100vw;
  height: 60px;
  background-color: white;
  position: fixed !important ;
  top: -60px;
  align-items: center;
  transition: all 0.4s ease 0.15s;
  z-index: 1000;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  justify-content: space-between;
}

.navbar-display {
  top: 0px;
}

.navbar-img-cnt {
  margin-left: 200px;
  transition: all 0.5s ease 0.15s;
}

.navbar-img {
  height: 40px;
}

.navbar-items {
  display: flex;
  color: black;
  margin-right: 200px;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  width: 40%;
  font-size: 16px;
  transition: all 0.5s ease 0.15s;
  /* font-family: "Montserrat", sans-serif; */
}

.navbar-item {
  display: flex;
  align-items: center;
  margin-top: 4px;
  cursor: pointer;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
}

.navbar-item-cnt {
  position: relative;
  height: 40px;
  display: flex;
  align-items: center;
  cursor: pointer;

  transition: all 0.2s ease 0.15s;
}

.navbar-item-cnt:hover .navbar-not-clicked {
  width: 100%;
}

.mobile-menu-item-cnt {
  display: none;
}

.navbar-clicked {
  position: absolute;
  border-radius: 10px;
  bottom: 0;
  width: 0;
  height: 4px;
  background-color: #254495;
  transition: all 0.3s ease 0.15s;
}

.navbar-not-clicked {
  position: absolute;
  border-radius: 10px;
  bottom: 0;
  width: 0px;
  height: 4px;
  background-color: #254495;
  transition: all 0.3s ease 0.15s;
}

.navbar-menu {
  display: none;
  width: 30px;
  height: 30px;
  /* border: 1px solid black; */
  margin-right: 20px;
  /* background-color: greenyellow; */
  cursor: pointer;
}

.mobile-menu-item-cnt {
  display: none;
}

.mobile-menu-item-cnt-display {
  display: none;
}

@media (max-width: 1430px) {
  .navbar-items {
    display: flex;
    margin-right: 50px;
    color: black;

    height: 100%;
    align-items: center;
    justify-content: space-between;
    width: 600px;
    font-size: 16px;
    /* font-family: "Montserrat", sans-serif; */
  }
}

@media (max-width: 1430px) {
  .navbar-img-cnt {
    margin-left: 40px;
  }
}

@media (max-width: 960px) {
  .navbar-menu {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .navbar-items {
    display: none;
  }

  .navbar-img-cnt {
    margin-left: 10px;
  }

  .mobile-menu-row {
    display: flex;
    /* background-color: rebeccapurple; */
    justify-content: space-between;
    align-items: center;

    width: 100%;
  }

  .navbar-display {
    flex-direction: column;
    justify-content: center;
    /* height: 300px; */
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .navbar-menu {
    margin-top: 15px;
    margin-bottom: 15px;
  }

  .mobile-menu-item-cnt-display {
    display: block;
    margin-bottom: 20px;
    margin-top: 20px;
    width: 100%;
  }
  .mobile-menu-item-cnt {
    display: none;
  }

  .mobile-row {
    /* background-color: red; */
    width: 100%;
    margin-left: 10px;
    padding: 10px;
    font-size: 16px;
    cursor: pointer;
  }

  .mobile-row:hover {
    background-color: #254495;
    color: white;
  }
}

